<template>
  <breadcrumb-list :items="breadcrumbs">
    <router-link
      :to="{ name: 'app.workspace.create' }"
      class="text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
    >
      <i class="fa fa-plus fa-sm fa-fw"></i>
      {{ $t("models.workspace.add") }}
    </router-link>
  </breadcrumb-list>

  <div
    v-if="!workspaces.empty"
    key="isWorkspaceCollectionNotEmpty"
    class="grid gap-5"
  >
    <workspace-item
      v-for="workspace in workspaces"
      :key="workspace.id"
      :workspace="workspace"
      show-destroy
      @destroyed="deleteModal"
    ></workspace-item>
  </div>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="error" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import { APP_URL } from "@/core/Config";
import WorkspaceCollection from "@/collections/WorkspaceCollection";
import ModelRequest from "@/request/ModelRequest";
import WorkspaceItem from "@/components/WorkspaceItem.vue";

export default {
  name: "AppWorkspaceList",
  components: { WorkspaceItem },
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.workspaces"), active: true }],
      workspaces: new WorkspaceCollection(),
      deleteSelect: null,
    };
  },
  created() {
    this.workspaces.fetchWithFilters();
  },
  methods: {
    logoSrc(logo) {
      return `${APP_URL}/images/workspaces/logos/${logo}`;
    },
    deleteModal(model) {
      this.deleteSelect = model;
      this.$refs.modal.toggle();
    },
    deleteData() {
      const request = new ModelRequest(this.deleteSelect);

      request.delete().then(() => {
        this.$refs.modal.toggle();

        this.workspaces.fetchWithFilters();
      });
    },
  },
};
</script>